/* Add a black background color to the top navigation */
.topnav {
    background-color: var(--background-primary);
    overflow: hidden;
}

.topnav a {
    float: left;
    color: var(--primary);
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
}

.topnav a:hover {
    background-color: var(--background-secondary);
    color: var(--secondary);
}

.topnav a.active {
    background-color: var(--background-secondary);
    color: var(--ternary);
}